<template>
  <div class="home">
    <div class="score">
      <div class="scorePad">
<!--        <span class="Pad">{{first}}:{{second}}</span>-->
        <el-row type="flex" justify="center" align="center">
            <el-col :span="12" class="first">
                <span>{{first}}</span>
                <span @click.enter ="add(0)" class="add" ></span>
                <span @click="sub(0)" class="sub"></span>
                <input v-model="firstTeam" class="firstTeam" >
              </el-col>
             <div class="reset" >
                 <span>:</span>
                 <span class="reset1" @click="reset(0)" ></span>
                 <span class="reset2" @click="reset(1)"></span>
             </div>
            <el-col :span="12" class="second">
                <span>{{second}}</span>
                <span @click="add(1)" class="add"></span>
                <span @click="sub(1)" class="sub"></span>
                <input  v-model="secondTeam" class="secondTeam" >
            </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Home',
  data() {
      return {
        count1: 0,
        count2: 0,
        firstTeam:'路人甲',
        secondTeam:'路人乙'
      }

  },
  mounted() {
      document.onkeydown = (e) =>{
          let key = window.event.keyCode;
          console.log(key)
          switch (key) {
            case 81: //Q
                this.add(0);
                break;
            case 65://A
                this.sub(0)
                break;
            case 87://W
                this.add(1);
                break;
            case 83: //S
                this.sub(1)
                break;
            case 32: //space
                this.reset(0)
                break;
            case 69: //e
                this.reset(1)
                break;
          }
      }
  },
    computed: {
     first() {
      if (this.count1 == 100) {
          this.count1 = 0
      }
      return this.count1<= 9? this.count1.toString() .padStart(2,'0'): this.count1.toString()
    },
    second() {
        if (this.count2 == 100) {
            this.count2= 0
        }
        return this.count2<= 9? this.count2.toString() .padStart(2,'0'): this.count2.toString()
    },

  },
  methods: {
     add(index) {
       if (index == 0) {
           this.count1++
       }  else if (index == 1) {
           this.count2++
       }
     },
     sub(index) {
        if (index ==0) {
            if (this.count1 == 0) return
            this.count1--
        } else if (index == 1) {
            if (this.count2 == 0) return
            this.count2--
        }
     },
     reset(i) {
          if (i == 1) {
              this.$prompt('输入左边分值','修改比分', {
                  confirmButtonText:'确定',
                  cancelButtonText:'取消',
                  inputPattern:/^([1-9]\d|\d)$/,
                  inputErrorMessage:'请输入0-99的数字'
              }).then(({value})=>{
                  console.log(value)
                  this.count1 = value
                  this.$prompt('输入右边分值','修改比分', {
                      confirmButtonText:'确定',
                      cancelButtonText:'取消',
                      inputPattern:/^([1-9]\d|\d)$/,
                      inputErrorMessage:'请输入0-99的数字'
                  }).then(({value})=>{
                      console.log(value)
                      this.count2 = value
                      this.$message({
                          type:'success',
                          message:'已修改比分'
                      })
                  })
              })
          } else {
              this.$confirm('是否重置比分', '提示', {
                  confirmButtonText:'确定',
                  cancelButtonText:'取消',
              }). then(() => {
                  this.count1 = this.count2 = 0
                  this.$message({
                      type:'success',
                      message:'已重置'
                  })
              })
          }
     }



  }

}
</script>
